import {
	getPageLayout
} from '@/api/userApi.js'
export default {
	name: 'StrictlyGoods',
	props: {
		// query: {
		//   type: Array,
		//   default: []
		// }
	},
	data() {
		return {
			goodsObj: '',
			list: []
		}
	},
	mounted() {
		this.getPageLayoutList()
	},
	methods: {
		goGoodsDetail(id) {
			this.$router.push({
				path: 'gooddetail/' + id
			})
		},
		// 获取严选好物列表
		getPageLayoutList() {
			let data = {
				parentId: '1-5'
			}
			getPageLayout(data).then(res => {
				if (res.code === '1000') {
					let list = res.data.list
					this.list = res.data.list
					list.forEach(item => {
						if (item.componentType === 5) {
							this.goodsObj = item
						}
					})
				}
			})
		},
		toForeach(item) {
			let arr = []
			if (item.list) {
				item.list.forEach(item => {
					arr.push(item)
				})
			}
			return arr
		}
	}
}
